body {
  background: #02060d;
  direction: rtl;
  text-align: right;
  font-family: "Assistant", sans-serif;
  font-size: 16px;
  letter-spacing: -.001em;
  font-weight: 200;
  line-height: 1.125;
  color: #ffffff;
}

.btn-burger-menu {
  position: absolute;
  top: 15px;
  left: 20px;
  z-index: 10;
  width: 43px;
  height: 32px;
  font-size: 0;
  background: transparent;
  border: 0;
}

.btn-burger-menu:hover .lines {
  border-color: #ffffff;
}

.btn-burger-menu:hover .lines::before, .btn-burger-menu:hover .lines::after {
  border-color: #ffffff;
}

.btn-burger-menu.is-active {
  top: 35px;
  left: 30px;
}

.btn-burger-menu.is-active:hover .lines {
  border-color: transparent;
}

.btn-burger-menu.is-active:hover .lines::before, .btn-burger-menu.is-active:hover .lines::after {
  border-color: #ffffff;
}

.burger-menu-list {
  position: absolute;
  top: 22px;
  left: 20px;
  z-index: 5;
  box-sizing: border-box;
  margin: 0;
  padding: 50px 60px;
  width: 405px;
  max-height: calc(100vh - 92px);
  height: auto;
  list-style: none;
  background-color: rgba(3, 6, 10, 0.9);
  border: 1px solid #afafaf;
  overflow: auto;
  transform: translateY(calc(-100% - 42px));
  transition: .25s;
}

@media only screen and (max-width: 1400px) {
  .burger-menu-list {
    padding: 30px 45px;
  }
}

@media only screen and (max-width: 1200px) {
  .burger-menu-list {
    width: 360px;
    padding: 20px 40px;
  }
}

@media only screen and (max-width: 992px) {
  .burger-menu-list {
    width: 340px;
    padding: 20px 30px;
  }
}

@media only screen and (max-width: 768px) {
  .burger-menu-list {
    padding: 20px 20px;
    width: 320px;
    max-height: calc(100vh - 44px);
  }
}

@media only screen and (max-width: 567px) {
  .burger-menu-list {
    top: 0;
    left: 0;
    width: 100%;
    max-height: 100vh;
    height: 100vh;
    text-align: center;
  }
}

.burger-menu-list.is-active {
  transform: translateY(0);
}

.burger-menu-list-li__link {
  display: inline-block;
  vertical-align: middle;
  font-family: "Assistant", sans-serif;
  font-size: 48px;
  line-height: 1.77083;
  letter-spacing: -0.01em;
  text-decoration: none;
  color: #ffffff;
  transition: .25s;
}

@media only screen and (max-width: 1400px) {
  .burger-menu-list-li__link {
    font-size: 38px;
  }
}

@media only screen and (max-width: 1200px) {
  .burger-menu-list-li__link {
    font-size: 28px;
  }
}

@media only screen and (max-width: 992px) {
  .burger-menu-list-li__link {
    font-size: 24px;
  }
}

@media only screen and (max-width: 768px) {
  .burger-menu-list-li__link {
    font-size: 22px;
  }
}

@media only screen and (max-width: 768px) {
  .burger-menu-list-li__link {
    font-size: 26px;
  }
}

.burger-menu-list-li__link:hover {
  text-decoration: none;
  color: #999999;
}

.burger-menu-list-li__link:focus {
  text-decoration: none;
  color: #666666;
}

.burger-menu-list-li.is-active .burger-menu-list-li__link {
  color: #494848;
}
